.triangle {
  background: #D8D8D8;
  height: 38px;
  position: relative;
  width: 38px;
}

.triangle:after {
  content: "";
  position: absolute;
  right: 25%;
  top: 50%;
}

.triangle.right:after {
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 19px solid #9B9B9B;
  margin-top: -12px;
}

.triangle.left:after {
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-right: 19px solid #9B9B9B;
  margin-top: -12px;
}

.flickity-slider button.flickity-prev-next-button {
  background-color: rgba(255, 255, 255, 0.75);
  border: 1px solid #e0e0e0;
  border-radius: 50%;
  color: #666;
  cursor: pointer;
  height: 44px;
  outline: none;
  position: absolute;
  text-transform: none;
  top: 24%;
  touch-action: manipulation;
  transform: translateY(-50%);
  transition: all 0.2s ease-in-out;
  width: 44px;
}

.flickity-slider button.flickity-prev-next-button:hover {
  background: #dddddd;
}

.flickity-slider button.flickity-prev-next-button:focus {
  box-shadow: none;
  outline: none;
}

.flickity-slider button.flickity-prev-next-button:active {
  background: #dddddd;
  opacity: 0.6;
}

.flickity-slider button.flickity-prev-next-button:disabled {
  opacity: 0.3;
  cursor: auto;
}

.flickity-slider button.flickity-prev-next-button.flickity-button.previous {
  left: auto;
  right: 50px;
}

.flickity-rtl .flickity-slider button.flickity-prev-next-button.flickity-button.previous {
  left: auto;
  right: 10px;
}

.flickity-slider button.flickity-prev-next-button.flickity-button.next {
  right: 0;
}

.flickity-rtl .flickity-slider button.flickity-prev-next-button.flickity-button.next {
  right: auto;
  left: 10px;
}

.flickity-slider button.flickity-prev-next-button svg {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}

.flickity-slider button.flickity-prev-next-button .arrow {
  fill: #333;
}

.flickity-slider .flickity-page-dots {
  bottom: 80px;
}

@media (min-width: 1100px) {
  .flickity-slider .flickity-page-dots {
    display: none;
  }
}

.flickity-slider .flickity-page-dots .dot {
  width: 5px;
  height: 5px;
  margin: 0 3px;
}

.flickity-slider .flickity-page-dots .dot.is-selected {
  opacity: 1;
}

