.wc-isotope-search {
  align-items: center;
  display: flex;
  justify-content: space-between;
  position: relative;
}

.wc-isotope-search label {
  width: 100%;
}

.wc-isotope-search input[type=search] {
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-style: italic;
  line-height: 100%;
  padding: 10px 15px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  -webkit-appearance: none;
}

.wc-isotope-search .toggle-filters {
  position: absolute;
  right: 15px;
}

.wc-isotope-search .toggle-filters i {
  font-size: 30px;
  font-weight: bold;
}

.wc-isotope-filters {
  box-shadow: 0 30px 70px rgba(255, 255, 255, 0.1);
  display: flex;
  height: 0;
  justify-content: space-between;
  margin-bottom: 100px;
  overflow: hidden;
  transition: height 0.3s ease-out;
}

.wc-isotope-filters .button-group {
  display: flex;
  flex-wrap: wrap;
}

.wc-isotope-filters button {
  display: inline-block;
  padding: 0.5em 1em;
  background: #EEE;
  border: none;
  border-radius: 7px;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.2));
  color: #222;
  font-size: 16px;
  text-shadow: 0 1px white;
  cursor: pointer;
}

.wc-isotope-filters button:hover {
  background-color: #8CF;
  text-shadow: 0 1px rgba(255, 255, 255, 0.5);
  color: #222;
}

.wc-isotope-filters button:active {
  box-shadow: inset 0 1px 10px rgba(0, 0, 0, 0.8);
}

.wc-isotope-filters button:active,
.wc-isotope-filters button.is-checked {
  background-color: #28F;
}

.wc-isotope-filters button.is-checked {
  color: white;
  text-shadow: 0 -1px rgba(0, 0, 0, 0.8);
}

.wc-isotope-filters .button-group:after {
  content: "";
  display: block;
  clear: both;
}

.wc-isotope-filters .button-group button {
  float: left;
  border-radius: 0;
  margin-bottom: 1px;
  margin-left: 0;
  margin-right: 1px;
}

.wc-isotope-filters .button-group button:first-child {
  border-radius: 0.5em 0 0 0.5em;
}

.wc-isotope-filters .button-group button:last-child {
  border-radius: 0 0.5em 0.5em 0;
}

